<template>
  <div  class="box">
    <ul>
      <li class="list">
        <p class="date">2018年3月的缴费详情</p>
        <div class="moneyBox">
          <p class="moneyList text1">
            <span class="personMoney">个人缴费</span>
            <span class="companyMoney">单位缴费</span>
            <span class="allMoney">缴费合计</span>
          </p>
          <p  class="moneyList number1">
            <span class="personMoney">201.1元</span>
            <span class="companyMoney">201.1元</span>
            <span class="allMoney">201.1元</span>
          </p>
        </div>
        <p class="btnPng" v-if="isBtn" @click="isListShowFn">
          <i class="el-icon-arrow-down"></i>
        </p>
        <div v-if="isList">
          <p class="moneyList listOne">
            <span>
              缴费基数
            </span>
            <span>
              3057.45
            </span>
          </p>
          <p  class="moneyList listTwo">
            <span>
              缴费类型
            </span>
            <span>
              正常缴费
            </span>
          </p>
          <p  class="moneyList listThree">
            <span>
              是否缴费
            </span>
            <span>
              足额缴费
            </span>
          </p>

        </div>
        <p class="btnPng" v-if="isBtn === false" @click="isListHideFn">
          <i class="el-icon-arrow-down"></i>
        </p>
      </li>
    </ul>
  </div>
</template>

<script>
  
  export default {
    data(){
      return{
        isBtn: true,
        isList: false
      }
    },
    methods:{
      isListShowFn(){
        this.isList = true
        this.isBtn = false
      },
      isListHideFn(){
        this.isList = false
        this.isBtn = true
      }
    }
  }
</script>

<style>
.box{
  background: #fff;
  font-size: 16px;
}
.list{
  border-top: 5px solid rgb(59,151,244);
  border-radius: 2px;
  padding:0 10px;
}
.date{
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
}
.moneyBox{
  border-bottom: 1px solid #ccc;
  height: 60px;
}
.moneyBox .text1{
  height: 30px;
  line-height: 30px;
  color: #afafaf;
}
.moneyBox .number1{
  height: 30px;
  line-height: 30px;
  color: #666;
}
.btnPng{
  height: 50px;
  margin-top: 2px;
  text-align: center;
  line-height: 50px;
}
.moneyList{
  display: flex;
  justify-content: space-between;
  align-items: center;
   flex-wrap:nowrap;
}
.personMoney{
  
  width: 20%;
}
.companyMoney{
  width: 20%;
}
.listOne,.listTwo,.listThree{
  height: 50px;
  border-bottom: 1px solid #ccc;
  line-height: 50px;
}
</style>



